.skillsSection{

    max-width: 100vw;
    min-height: 100vh;
    padding-top: 3rem!important;
    padding-bottom: 3rem!important;
    display: flex;
    flex-wrap: wrap;
    text-align: center;
    align-items: center;
}
a{
    /*color: inherit !important;*/
}
.MuiCardMedia-img{
    background-size: 50% !important;
}
.mySkills ul li .skillIcon{
    align-self: center;
    text-align: center;
}

.mySkills ul {
    display: flex;
    justify-content: space-evenly;
    list-style-type: none;
    padding-left: 0;
    font-size: 1.3rem;

}

.myCard{
    display: inline-block ;
    margin-top: 20px;
    margin-left: 15px;
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06)!important;
   height: 300px;
}
.mySkills{
    /*width: 80vw;*/
    /*position: absolute;*/
    /*top: 25%;*/
}
.flutterIcon{
    display: inline-block;
    width: 57px;
    height: 70px;
    background-size: 58px;
    background-repeat: no-repeat;

}
.springIcon{
    display: inline-block;
    width: 70px;
    height: 70px;
    background-size: 69px;
    background-repeat: no-repeat;
}
.skill{
    margin: 16px;
}
.MuiTypography-body2 {
    font-size: 0.775rem !important;
}
@media (max-width: 800px){
    .mySkills ul {
        display: block;

    }
    .mySkills ul {
        display: block;

    }

}